button{
    width: 3em;
    height: 3em;
    font-size: 1.5em;
    border: none;
    border-radius: 50%;
    margin: 0.3em;
}

button:hover{
    cursor: pointer;
}

.wrapper{
    background-color: black;
    border-radius: 1em;
    padding: 1em;
}

.orange{
    background-color: #FF9500;
    color: white;
    font-weight: 600;
}

.lightgrey{
    background-color: lightgrey;
    color: black;
    font-weight: 600;
}

.darkgrey{
    background-color: #505050;
    color: white;
    font-weight: 500;
    font-weight: 600;
}

.resultado{
    color: white;
    text-align: right;
    padding-right: 0.5em;
    font-size: 3.5em;
    font-weight: 400;
}

#zero{
    width: 6.5em;
    border-radius: 1.4em;
}